<template>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <slot></slot>
      </div>
      <!-- <div class="swiper-pagination"></div> -->
    </div>
</template>
<script>
import Swiper from 'swiper'
import 'swiper/css/swiper.min.css'
export default {
  props: ['swiper', 'myclassname'],
  data () {
    return {

    }
  },
  mounted () {
    /* eslint-disable no-new */
    new Swiper('.' + this.myclassname, {
      slidesPerView: this.swiper,
      spaceBetween: this.myclassname === 'actorsswiper' ? 33 : 10,
      freeMode: true,
      observer: true, // (OB//)swiper/swiperswiper
      observeParents: true // observeSwiperSwiperwindow.resizeSwiper
    })
  }
}
</script>
<style lang="scss" scoped>
.swiper-slide{
    img{
        width: 100%;
    }
}
</style>
